import uniqueid from "lodash/uniqueid";
export function uniqueIDFromPrefix(suffix) {
    return (suffix + uniqueid());
}
export function getRequiredID(props, suffix) {
    if (props.id) {
        return props.id;
    }
    else {
        return uniqueIDFromPrefix(suffix);
    }
}
export function getOptionalID(props, suffix) {
    if (props.id) {
        // we want an ID
        if (typeof props.id === "string") {
            // Handled by parent component
            return props.id;
        }
        else if (typeof props.id === "boolean" && suffix) {
            return uniqueIDFromPrefix(suffix);
        }
        throw new Error("To generate and ID, you must provide a suffix.");
    }
    else {
        return null;
    }
}
