import { addRoutes } from "@core/application";
import React from "react";
import { Route } from "react-router-dom";
import SignInPage from "./Authenticate/SignInPage";
import PasswordPage from "./Authenticate/PasswordPage";
import RecoverPasswordPage from "./Authenticate/RecoverPasswordPage";
addRoutes([
    React.createElement(Route, { exact: true, path: "/authenticate/signin", component: SignInPage }),
    React.createElement(Route, { exact: true, path: "/authenticate/password", component: PasswordPage }),
    React.createElement(Route, { exact: true, path: "/authenticate/recoverpassword", component: RecoverPasswordPage }),
]);
